<template>
    <div>
        <el-container>
            <el-header>
                选择水库
            </el-header>
            <el-main>
                <el-row :gutter="15">
                    <el-col :span="18">
                        <el-card shadow="hover"
                                 :body-style="{
                                    flex: '1',
                                    display: 'flex',
                                    padding: '10px',
                                 }">

                            <baidu-map
                                    class="select-map-baidu"
                                    :scroll-wheel-zoom="true"
                                    :style=" zoomBack"
                                    :center="center"
                                    :zoom="zoom"
                                    @moving="updateCenterValue"
                                    @moveend="updateCenterValue"
                                    @zoomend="updateCenterValue"
                                    @click="handleMapClick"
                            >
                                <bm-geolocation
                                        anchor="BMAP_ANCHOR_BOTTOM_RIGHT"
                                        :showAddressBar="true"
                                        :autoLocation="true"
                                        @locationSuccess="handleLocationSuccess"
                                ></bm-geolocation>
                                <bm-marker
                                        v-if="showPointer"
                                        :position="centerDisplay"
                                        :icon="pointer"
                                        :offset="{height: 0, width: 15}"
                                ></bm-marker>
                            </baidu-map>
                        </el-card>
                    </el-col>
                    <el-col :span="6">
                        <el-card class="map-info" shadow="hover">
                            <el-form class="reservoir-select">
                                <el-form-item label="系统已记录:">
                                    <el-select v-model="selectedReservoir"
                                               placeholder="请选择"
                                               @change="handleSelectChange"
                                    >
                                        <el-option
                                                v-for="item in reservoirList"
                                                :label="item.name"
                                                :value="item.name"
                                                :key="item.id"
                                        >{{item.name}}
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-form>
                            <el-divider/>
                            <el-form label-position="right">
                                <el-form-item label="经度:">
                                    <el-input v-model.number="centerDisplay.lng" @change="syncCenterValue"></el-input>
                                </el-form-item>
                                <el-form-item label="纬度:">
                                    <el-input v-model.number="centerDisplay.lat" @change="syncCenterValue"></el-input>
                                </el-form-item>
                                <el-form-item>
                                    <el-button :disabled="checkCenterValueDifferent" @click="syncCenterValue">定位
                                    </el-button>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="success" :disabled="selectedReservoir===''"
                                               @click="submitReservoirName">确定
                                    </el-button>
                                </el-form-item>
                            </el-form>
                        </el-card>
                    </el-col>
                </el-row>


            </el-main>
        </el-container>
    </div>
</template>

<script>

    export default {
        name: "selectOnMap",
        components: {},
        data: function () {
            return {
                selectedReservoir: '',
                center: {
                    lng: 116.404,
                    lat: 39.915,
                },
                centerDisplay: {
                    lng: 116.404,
                    lat: 39.915,
                },
                pointer: {
                    url: '/marker.png',
                    size: {width: 50, height: 50},
                },
                showPointer: false,
                zoom: 11,
                reservoirList: [],
            }
        },
        methods: {
            handleSelectChange(value) {
                this.showPointer = false;
                const list = this.reservoirList;
                for (let i = 0; i < list.length; i++) {
                    if (list[i].name === value) {
                        const r = list[i];
                        this.selectedReservoir = r.name;
                        this.centerDisplay.lng = 0.5 * (Number(r.longitudeLeft) + Number(r.longitudeRight));
                        this.centerDisplay.lat = 0.5 * (Number(r.latitudeLeft) + Number(r.latitudeRight));
                        this.syncCenterValue();
                        this.zoom = 11;
                        break;
                    }
                }
            },
            handleMapClick(e) {
                this.showPointer = true;
                this.centerDisplay.lng = e.point.lng;
                this.centerDisplay.lat = e.point.lat;
                for (let i = 0; i < this.reservoirList.length; i++) {
                    if (e.point.lng >= this.reservoirList[i].longitudeLeft &&
                        e.point.lng <= this.reservoirList[i].longitudeRight &&
                        e.point.lat >= this.reservoirList[i].latitudeRight &&
                        e.point.lat <= this.reservoirList[i].latitudeLeft) {
                        this.axios.get("/api/map/isWaterArea?lng=" + e.point.lng + "&lat=" + e.point.lat + "&zoom=" + this.zoom).then((response) => {
                            if (response.data) {
                                this.selectedReservoir = this.reservoirList[i].name;
                            } else {
                                this.selectedReservoir = "";
                            }
                        });
                    } else {
                        this.selectedReservoir = "";
                    }
                }
            },
            syncCenterValue() {
                this.center.lng = this.centerDisplay.lng;
                this.center.lat = this.centerDisplay.lat;
            },
            updateCenterValue(e) {
                const c = e.target.getCenter();
                this.center.lng = c.lng;
                this.center.lat = c.lat;
                this.zoom = e.target.getZoom()
            },
            handleLocationSuccess(e) {
                this.centerDisplay.lng = e.point.lng;
                this.centerDisplay.lat = e.point.lat;
                this.center.lng = e.point.lng;
                this.center.lat = e.point.lat;
                for (let i = 0; i < this.reservoirList.length; i++) {
                    if (e.point.lng >= this.reservoirList[i].longitudeLeft &&
                        e.point.lng <= this.reservoirList[i].longitudeRight &&
                        e.point.lat >= this.reservoirList[i].latitudeRight &&
                        e.point.lat <= this.reservoirList[i].latitudeLeft) {
                        this.axios.get("/api/map/isWaterArea?lng=" + e.point.lng + "&lat=" + e.point.lat + "&zoom=" + this.zoom).then((response) => {
                            if (response.data) {
                                this.selectedReservoir = this.reservoirList[i].name;
                            } else {
                                this.selectedReservoir = "";
                            }
                        });
                    } else {
                        this.selectedReservoir = "";
                    }
                }
            },
            submitReservoirName() {
                if (this.selectedReservoir === "") {
                    return;
                }
                this.axios.get("api/setReservoirId", {
                    params: {
                        rname: this.selectedReservoir
                    }
                }).then((response) => {
                    if (response.data === "success") {
                        this.$store.commit('setReservoir', this.selectedReservoir);
                        this.$router.push('/')
                    } else {
                        //alert("需要输入正确的水库名称");
                    }
                });
            }
        },
        computed: {
            checkCenterValueDifferent: function () {
                return this.center.lng === this.centerDisplay.lng && this.center.lat === this.centerDisplay.lat;
            },
            zoomBack: function () {
                return {zoom: Math.min(1600 / window.screen.width, 1) + ''};
            }
        },
        mounted() {
            this.axios.get('/api/reservoirInfo/reservoir').then((response) => {
                if (response.status === 200) {
                    this.reservoirList = response.data;

                } else {
                    console.log("error")
                }
            })
        }
    }
</script>

<style scoped>
    .el-container {
        min-height: 100%;
    }

    .el-main {
        height: 100%;
        display: inline-flex;
        padding: 0 5px 0 20px;
    }

    .el-row {
        width: 100%;
        overflow: hidden;
    }

    .el-card {
        border: 1px solid #d4d4d4;
        flex: 1;
        display: flex;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .map-info {
        display: inline-flex;
        flex-direction: column;
    }

    .el-col-18 {
        height: 100%;
        overflow-y: auto;
        flex: 1;
        display: flex;
    }

    .select-map-baidu {
        flex: 1;
    }
</style>
